.home {
    width: 100%;
    height: 100vh;
    padding-top: 56px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: #353434;
}

.home-spin {
    width: 100%;
    height: 100%;
    top: -56px;
    background-color: rgba(255, 255, 255, 0);
    z-index: 999;
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}