.left-panel {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    height: auto;
    width: 100%;
    height: 100%;
    /* background: #444; */
    padding: 2em;
    overflow-x: hidden;
    color: #fff;
}

.analysis-image {
    height: 30em;
    width: auto;
}

.search-card {
    width: 100%;
    margin-top: 2em;
}

.search-card-container {
    width: 100%;
}

.sgf-card {
    width: 100%;
    height: auto;
    margin-top: 2em;
}

.gamemode-card {
    width: 100%;
    height: auto;
    min-height: 25em;
    overflow: hidden;
}

.gamemode-button-list {
    margin: 0.02em;
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    margin: 0 auto;
    font-size: 0.24em;
}

.label {
    font-size: 1em;
    font-weight: bold;
}

.choose-ai-select {
    width: 100%;
    height: auto;
}

.ask-help-btn {
    margin-left: 2em;
}
