.game-panel {
    height: 100%;
    width: auto;
    min-width: 570px;
    /* background: #444; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    overflow: hidden;
    border-left: 0.2em solid rgb(164, 162, 163);
    border-right: 0.2em solid rgb(164, 162, 163);
    padding: 24px;
}

.goban-spin {
    width: 100%;
    height: 100%;
    background-color: rgba(216, 213, 213, 0);
    z-index: 999;
    top: -56px;
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.message-container {
    position: relative;
    width: 516px;
    height: 40px;
    margin-bottom: 20px;
}