.right-panel {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  height: 100%;
  min-width: 200px;
  width: 100%;
  /* background: #444; */
  padding: 2em;
  overflow-x: hidden;
}

.win-rate-card {
  width: 100%;
  height: auto;
}

.code {
  width: 100%;
  height: 600px;
  color: #fff;
}

.line-canvas {
  width: 100%;
  height: 20em;
  margin-bottom: 0.2em;
  margin-top: 0em;
}

.tree-card {
  width: 100%;
  padding: 0.2em 0 0 0;
  height: auto;
}

.tree-canvas {
  width: 100%;
  height: auto;
  background-color: #fff;
}