.login-container {
	padding-top: 56px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.form-container {
	position: relative;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: row;
	font-family: 'Montserrat', sans-serif;
	height: 500px;
	width: 750px;
	margin-top: 100px;
	box-sizing: border-box;
	transition: all 0.6s ease-in-out;
	border-radius: 17px;
	overflow: hidden;
	-moz-box-shadow:1px 2px 20px #333333; 
	-webkit-box-shadow:1px 2px 20px #333333; 
	box-shadow:1px 2px 20px #333333;
}

.sign-in-container {
	position: relative;
	flex: 1;
	width: 50%;
	z-index: 2;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 20% 5% 20% 5%;
	background: #fff;
}

.overlay-container {
	position: relative;
	flex: 2;
	width: 50%;
	height: 100%;
	max-width: 375px;
	overflow: hidden;
	transition: transform 0.6s ease-in-out;
	z-index: 100;
	background:url(../../assets/goban1.jpeg)
}

.form-container h1 {
	font-weight: bold;
	margin: 0;
}

.form-container h2 {
	text-align: center;
}

.form-container p {
	font-size: 14px;
	font-weight: 100;
	line-height: 20px;
	letter-spacing: 0.5px;
	margin: 20px 0 30px;
}

.form-container span {
	font-size: 12px;
}

.forget-password {
	color: #333;
	font-size: 14px;
	text-decoration: none;
	margin: 15px 0;
}

.signin-h1{
	padding-bottom: 10px;
}

.login-h1{
	color: #ffffff;
	
}

.login-button {
	width: 200px;
	border-radius: 20px;
	border: 1px solid #673AB7;
	background-color: #673AB7;
	color: #FFFFFF;
	font-size: 12px;
	font-weight: bold;
	padding: 12px 45px;
	letter-spacing: 1px;
	text-transform: uppercase;
	transition: transform 80ms ease-in;
}

.login-button:active {
	transform: scale(0.95);
}

.login-button:focus {
	outline: none;
}

.login-button.ghost {
	background-color: transparent;
	border-color: #FFFFFF;
}

.sign-in-container input {
	background-color: #eee;
	border: none;
	padding: 12px 15px;
	margin: 8px 0;
	width: 100%;
}

.login-container .right-panel-active .sign-in-container {
	transform: translateX(100%);
}

.sign-up-container {
	left: 0;
	width: 50%;
	opacity: 0;
	z-index: 1;
}

.login-container .right-panel-active .sign-up-container {
	transform: translateX(100%);
	opacity: 1;
	z-index: 5;
	animation: show 0.6s;
}

@keyframes show {
	0%, 49.99% {
		opacity: 0;
		z-index: 1;
	}
	
	50%, 100% {
		opacity: 1;
		z-index: 5;
	}
}

.login-container .right-panel-active .overlay-container{
	transform: translateX(-100%);
}

.overlay {
	/* background: #FF416C;
	background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);
	background: linear-gradient(to right, #FF4B2B, #FF416C); */
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 0 0;
	color: #FFFFFF;
	position: relative;
	left: -100%;
	height: 100%;
	width: 200%;
  	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.login-container.right-panel-active .overlay {
  	transform: translateX(50%);
}

.overlay-panel {
	position: absolute;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	padding: 0 40px;
	text-align: center;
	top: 0;
	height: 100%;
	width: 50%;
	transform: translateX(0);
	transition: transform 0.6s ease-in-out;
}

.overlay-left {
	transform: translateX(-20%);
}

.login-container.right-panel-active .overlay-left {
	transform: translateX(0);
}

.overlay-right {
	right: 0;
	transform: translateX(0);
}

.login-container.right-panel-active .overlay-right {
	transform: translateX(20%);
}

.social-container {
	margin: 20px 0;
}

.social-container a {
	border: 1px solid #DDDDDD;
	border-radius: 50%;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 0 5px;
	height: 40px;
	width: 40px;
}